import { useEffect, useState, memo, useCallback } from 'react';

import { Table, Typography } from 'antd';
import Column from 'antd/lib/table/Column';

import useCurrencies from './hooks/useGetCurrencies';
import useTicker from './hooks/useTicker';

import './App.css';
import 'antd/dist/antd.css';

const PAGE_SIZE = 15;

const Currency = memo(({ id }) => {
    const [value, setValue] = useState(null);

    const updateValue = useCallback((data) => {
        if (data.method === 'ticker' && data.params.symbol === `${id}USD`) {
            setValue(data.params.last);
        }
    }, [id]);

    const { subscribe, unsubscribe } = useTicker(updateValue);

    useEffect(() => {
        subscribe({ ticker: id });

        return () => unsubscribe({ ticker: id });
    }, [id, subscribe, unsubscribe]);

    return value ? '$' + value : 'Нет данных';
});

const App = () => {
    const [page, setPage] = useState(1);
    const { currencies } = useCurrencies();

    console.log(currencies);

    return (
        <div className="App">
            <Table
                dataSource={currencies}
                className="currencies-table"
                pagination={{
                    current: page,
                    onChange: setPage,
                    pageSize: PAGE_SIZE,
                    size: 'small',
                    showSizeChanger: false,
                }}
                rowKey={item => item.symbol}
            >
                <Column
                    key="name"
                    title="Криптовалюта"
                    render={(_, item) => {
                        return (
                            <Typography.Text>
                                {item.symbol.replace(/USD/, '')}
                            </Typography.Text>
                        );
                    }}
                    dataIndex="id"
                />
                <Column
                    key="quotation"
                    title="Котировка (USD)"
                    render={(_, item) => <Currency id={item.symbol.replace(/USD/, '')} />}
                />
            </Table>
        </div>
    );
}

export default App;
